<script>
const NORMAL_BORDER = "7px",
    SING_BORDER = "12PX";
const STEP = 10;

import createLine from "../../../core/auxiliary/createLine-minxs";

export default {
    name: "scaleplate",
    data() {
        return {
            allLine: []
        };
    },
    components: {
        myline: () => import("./line/line")
    },
    mixins: [createLine],
    render() {
        return (
            <div class="scaleplate">
                {this.lineList.map(it => {
                    return (
                        <myline
                            params={it}
                            type={it.type}
                            data-uid={it.uid}
                        ></myline>
                    );
                })}
                {["col", "row"].map(it => {
                    return (
                        <ul
                            draggable="false"
                            class="map-container"
                            direction={it}
                            v-createLine
                        >
                            {Array.apply(this, { length: 140 }).map(
                                (ele, index) => {
                                    let breadth = NORMAL_BORDER;
                                    let showText = false;
                                    let style;
                                    if (index % 5 === 0 && index !== 0) {
                                        breadth = SING_BORDER;
                                        showText = true;
                                    }
                                    if (it == "col") {
                                        style = {
                                            left: index * STEP + "px",
                                            height: breadth
                                        };
                                    } else {
                                        style = {
                                            top: index * STEP + "px",
                                            width: breadth
                                        };
                                    }

                                    return (
                                        <li
                                            style={style}
                                            ondragstart="return false"
                                        >
                                            {showText ? (
                                                <span> {index * 1}</span>
                                            ) : (
                                                ""
                                            )}
                                        </li>
                                    );
                                }
                            )}
                        </ul>
                    );
                })}
            </div>
        );
    }
};
</script>

<style lang="less" scoped>
@import url("./style.less");
</style>
