/**
 * 产生辅助线
 */
import { mapState } from "vuex";

export default {
    computed: {
        ...mapState({
            lineList: state => state.line.list
        }),
    },
    directives: {
        createLine(el, bind, vnode) {
            /**
             * 梳理
             * 1. 标尺被点击的时候
             *    -- 根据标尺方向，往vuex里面的state.line.list队列push对象
             *    -- 同时存储一些变量
             * 
             * 2. 鼠标移动过程
             *    -- 改变最后增加的"标尺线"的left和top
             * 
             * 3. 鼠标松开
             *    -- 根据鼠标位置，判断是否需要删除刚才添加的"标尺线"
             */

            let box = el; // 保存引用
            let _this = vnode.context; //vue上下文
            box.onmousedown = e => {
                e.preventDefault();

                let elNavHeaderHeight = document.querySelector('.nav-header').offsetHeight,
                    elNanSideWidth = document.querySelector('.nav-side').offsetWidth;

                let disX = e.clientX - box.offsetLeft - elNanSideWidth,
                    disY = e.clientY - box.offsetTop - elNavHeaderHeight;

                let direction = box.getAttribute("direction");

                let line = { left: 0, top: 0, uid: (Date.now() * Math.random()).toString(36) }

                if (direction == "col") {
                    line.type = "col"
                    Object.assign(line, { top: disY });
                } else {
                    line.type = "row"
                    Object.assign(line, { left: disX });
                }

                _this.lineList.push(line);

                let top, left;
                let lastline = _this.lineList.length - 1;

                document.onmousemove = e => {
                    if (direction == "col") {
                        top = e.clientY - elNavHeaderHeight;
                        _this.lineList[lastline].top = top;
                    } else {
                        left = e.clientX - elNanSideWidth;
                        _this.lineList[lastline].left = left;
                    }
                };

                document.onmouseup = () => {
                    // 越界
                    if (
                        parseFloat(_this.lineList[lastline].top) < 0 ||
                        parseFloat(_this.lineList[lastline].left) < 0
                    ) {
                        _this.lineList.pop();
                    }

                    document.onmousemove = null;
                    document.onmouseup = null;
                    if (typeof box.releaseCapture !== "undefined") {
                        box.releaseCapture();
                    }
                };
            };
        }
    }
}